.bar-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  width: 100%;
  box-sizing: border-box;
  padding: 0 12px;
  margin-top: -100px;
  gap: 12px;
  max-width: 100vw;
  background-color: white;
  position: fixed;
  z-index: 1000;
}

.logo-section {
  display: flex;
  align-items: center;
  min-width: 0;
  flex-shrink: 0;
  padding-left: 12px;
}

.logo {
  height: 85px;
}

.logo-text {
  width: 100px;
  font-weight: 550;
  font-size: 14.4px;
  max-width: 90px;
}

.search-section {
  display: flex;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 1;
  min-width: 0;
  max-width: 100%;
  overflow: hidden;
  background-color: white;
  border: 1.5px solid #ccc;
  border-radius: 999px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  padding: 4px 12px;
  gap: 12px;
  margin-top: 10px;
}

.search-input {
  display: flex;
  flex: 1;
  outline: none;
  font-size: 16px;
  padding: 10px 0;
  border: none;
}

.search-button,
.cart-button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: fit-content;
  overflow: none;
  background: transparent;
  border: none;
  padding: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  border-radius: 50%;
}

.search-button:hover,
.cart-button:hover {
  background-color: #f2f2f2;
}

.search-button img,
.cart-button img {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.auth-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-shrink: 0;
  white-space: nowrap;
  padding-right: 12px;
  gap: 8px;
}

.signup-button {
  padding: 10px 24px;
  border: none;
  background-color: black;
  color: white;
  border-radius: 999px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.signup-button:hover {
  background-color: #333;
}

.login-button {
  padding: 9px 24px;
  border: 2px solid black;
  background-color: transparent;
  color: black;
  border-radius: 999px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.login-button:hover {
  background-color: black;
  color: white;
}

body {
  margin: 0;
  overflow-x: hidden;
}

@media (max-width: 768px) {
  .bar-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    padding: 8px 12px;
    gap: 8px;
    margin-top: -115px;
  }

  .logo-section {
    display: flex;
    align-items: center;
    min-width: 0;
    flex-shrink: 0;
    padding-left: 8px;
  }

  .logo {
    height: 90px;
    flex-shrink: 0;
    object-fit: contain;
  }

  .logo-text {
    display: none;
  }

  .search-section {
    flex: 1;
    min-width: 150px;
    padding: 4px 10px;
  }

  .search-input {
    min-width: 0;
  }

  .auth-section {
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-shrink: 0;
  }

  .login-button,
  .signup-button {
    width: 60px;
    margin-left: 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    padding: 14px 20px;
  }

  .signup-button {
    font-size: 14.4px;
    padding: 13px 20px;
  }

  .login-button {
    font-size: 14.4px;
    padding: 11px 20px;
  }
}

.hamburger-button {
  display: none;
  background: none;
  color: black;
  border: none;
  cursor: pointer;
  margin-left: 10px;
}

.auth-desktop {
  display: flex;
  align-items: center;
  gap: 10px;
}

.mobile-auth-menu {
  display: none;
}

.logout-button {
  background-color: black;
  color: white;
  border-radius: 50px;
  padding: 6px 8px;
}

@media (max-width: 768px) {
  .auth-desktop {
    display: none;
  }

  .hamburger-button {
    display: block;
    font-size: 55px;
  }

  .mobile-auth-menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 60px;
    right: 10px;
    background-color: transparent;
    padding: 10px 20px;
    border: 4px lightgray;
    border-radius: 20px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
    gap: 10px;
    z-index: 9999;
  }
}

.cart-button {
  position: relative;
  background: none;
  border: none;
  cursor: pointer;
}

.cart-badge {
  position: absolute;
  top: 4px;
  right: -4px;
  background-color: red;
  color: white;
  border-radius: 999px;
  width: 16px;
  font-size: 12px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
}
