.admin-wrap {
  padding: 24px;
  max-width: 1200px;
  margin: auto;
  font-family: 'Segoe UI', sans-serif;
  color: #000;
  background-color: #fff;
}

h2, h3 {
  color: #000;
  margin-bottom: 16px;
}

.admin-search-input {
  padding: 10px 16px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 16px;
  width: 100%;
  max-width: 400px;
  color: #000;
  background-color: #fff;
}

.admin-user-table,
.admin-cart-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 32px;
}

.admin-user-table th,
.admin-user-table td,
.admin-cart-table th,
.admin-cart-table td {
  padding: 12px;
  border: 1px solid #ccc;
  text-align: left;
  color: #000;
}

.admin-user-table th,
.admin-cart-table th {
  background-color: #f2f2f2;
}

.admin-btn {
  padding: 8px 16px;
  margin: 4px;
  border: none;
  border-radius: 6px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s ease;
}

.admin-btn:hover {
  background-color: #333;
}

.admin-btn.reject,
.admin-btn.delete {
  background-color: #d32f2f;
}

.admin-btn.reject:hover,
.admin-btn.delete:hover {
  background-color: #b71c1c;
}

.admin-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin: 32px 0;
}

.admin-filter-row {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 16px 0;
}

.admin-product-create,
.admin-product-list {
  margin-top: 48px;
}

.admin-product-form input[type="text"],
.admin-product-form input[type="number"],
.admin-product-form input[type="file"] {
  display: block;
  margin-bottom: 16px;
  padding: 8px;
  width: 100%;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: #fff;
  color: #000;
}

.admin-product-form button {
  margin-top: 8px;
}

.admin-product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 16px;
  margin-top: 16px;
}

.admin-product-card {
  background-color: #fff;
  padding: 16px;
  border-radius: 8px;
  text-align: center;
  border: 1px solid #ccc;
  color: #000;
}

.admin-product-card img {
  border-radius: 6px;
  margin-bottom: 8px;
  max-width: 100%;
}

.message {
  margin-top: 12px;
  font-weight: 500;
}

details summary {
  cursor: pointer;
  color: #000;
  font-weight: 500;
  margin-bottom: 8px;
}

@media (max-width: 768px) {
  .admin-filter-row {
    flex-direction: column;
  }

  .admin-search-input {
    max-width: 100%;
  }

  .admin-pagination {
    flex-direction: column;
    gap: 8px;
  }

  .admin-product-form input {
    max-width: 100%;
  }

  .admin-wrap {
    padding: 16px;
  }
}
