.signupText {
  margin-bottom: 25px;
  font-size: 30px;
}

.auth-container {
  max-width: 400px;
  margin: 80px auto;
  padding: 40px 24px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.auth-form input {
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.auth-form button {
  padding: 12px;
  font-size: 16px;
  font-weight: 600;
  background-color: black;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.auth-form button:hover {
  background-color: #333;
}
