.checkout-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 32px;
  text-align: center;
}

.qr-image {
  width: 250px;
  margin: 16px 0;
}

.upload-section {
  margin-top: 32px;
}

.upload-section input[type="file"] {
  font-size: 16px;
  padding: 8px 12px;
  line-height: 1;
  height: 40px;
  box-sizing: border-box;
}

.upload-section button {
  padding: 8px 16px;
  background-color: black;
  white-space: nowrap;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color ease 0.3s;
}

.upload-section button:hover {
  background-color: #2c2c2c;
}
