.product-list {
  display: grid;
  margin-left: 20px;
  margin-right: 20px;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  background-color:#F5F5F5 ;
}

@media (max-width: 768px) {
  .product-list {
    grid-template-columns: repeat(2, 1fr);
  }
}



.product-card {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.8s ease, transform 0.8s ease;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.product-card.animate {
  opacity: 1;
  transform: scale(1);
}

.product-card img {
  width: 100%;
  height: auto;
  max-height: 180px;
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: 12px;
}

.product-name {
  font-size: 17.6px;
  font-weight: bold;
  margin-bottom: 4px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  max-width: 100%;
}

.product-owner {
  font-family:'SakerSansLight';
  src: url('../assets/SakerSansLight.ttf');
  font-size: normal;
  color: white;
  background-color: black;
  margin-bottom: 4px;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 4px;
}

.product-phone {
  font-size: 10px;
  color: #777;
  margin-bottom: 8px;
}

.product-price {
  font-family:'SakerSansMedium';
  src: url('../assets/SakerSansMedium.ttf');
  font-size: 22px;
  font-weight:bolder ;
  color: #333;
  margin-bottom: 12px;
}

.add-to-cart {
  padding: 8px 16px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 999px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.add-to-cart:hover {
  background-color: #333;
}

.add-to-cart.added {
  background-color: #28a745;
  transform: scale(1.08);
  box-shadow: 0 0 8px rgba(40, 167, 69, 0.4);
}

.add-to-cart.added:active {
  transform: scale(0.95);
}

.fab-info {
  position: fixed;
  bottom: 24px;
  right: 24px;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: 53px;
  height: 53px;
  font-size: 24px;
  cursor: pointer;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.info-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1001;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  text-align: center;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 8px;
  right: 12px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
