.cart-container {
  padding: 24px;
  font-family: 'Segoe UI', sans-serif;
  max-width: 900px;
  margin: auto;
  background-color: #f9f9f9;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.backmenu-btn {
  border: 1px solid lightgray;
  color: black;
  background-color: transparent;
  padding: 8px 14px;
  font-size: 17px;
  font-weight: 400;
  cursor: pointer;
  transition: 0.3s ease;
}


.backmenu-btn:hover {
  background-color: lightgray;
}

h2 {
  font-size: 26px;
  color: #222;
  margin: 0;
}

.cart-items {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.cart-card {
  display: flex;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  align-items: center;
}

.cart-image {
  width: 160px;
  height: 160px;
  object-fit: cover;
  border-radius: 8px;
  flex-shrink: 0;
}

.cart-info {
  flex: 1;
  padding: 12px 16px;
}

.cart-info h3 {
  font-size: 17px;
  margin-top: 0;
  margin-bottom: 8px;
}

.cart-info p {
  margin: 4px 0;
  font-size: 14px;
  color: #444;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 10px;
}

.quantity-controls button {
  padding: 4px 10px;
  font-size: 16px;
  background-color: #ddd;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.2s ease;
}

.quantity-controls button:hover {
  background-color: #bbb;
}

.quantity-controls span {
  font-size: 15px;
  font-weight: 600;
  min-width: 24px;
  text-align: center;
}
.value-input{
  width: 30px;
}

.remove-btn {
  margin-top: 12px;
  padding: 6px 12px;
  background-color: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 13px;
  cursor: pointer;
}

.remove-btn:hover {
  background-color: #c0392b;
}

.cart-summary {
  margin-top: 30px;
  padding: 16px;
  border-top: 1px solid #ddd;
  text-align: right;
  font-size: 16px;
  color: #222;
}

.cart-summary h3 {
  margin-top: 8px;
  font-size: 20px;
  color: #111;
}

.cart-actions {
  margin-top: 20px;
  text-align: center;
}

.checkout-btn,
.back-btn,
.orders-btn {
  padding: 12px 26px;
  font-size: 16px;
  background-color: #222;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color ease 0.3s;
}

.checkout-btn:hover,
.back-btn:hover,
.orders-btn:hover {
  background-color: #333;
}

.empty-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 250px;
}

.cart-header-actions {
  display: flex;
  justify-content: flex-start;
  gap: 12px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.orders-btn:hover,
.back-btn:hover {
  background-color: solid lightgray;
}

@media (max-width: 768px) {
  .backmenu-btn {
    padding: 8px 20px;
  }
  .value-input{
    width: 15px;
  }
}
