.my-orders {
  padding: 24px;
  max-width: 1000px;
  margin: auto;
  font-family: 'Segoe UI', sans-serif;
  background-color: white;
  color: #000;
}

h2 {
  font-size: 24px;
  margin-bottom: 24px;
  text-align: center;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 24px;
  justify-content: center;
}

.filters input[type="text"],
.filters input[type="date"] {
  padding: 10px 16px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 240px;
  background-color: #fff;
  color: #000;
}

.order-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 24px;
  background-color: #fff;
  color: #000;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.order-card img {
  border-radius: 6px;
  margin-bottom: 12px;
  max-width: 100%;
  height: auto;
}

.order-card p {
  margin: 4px 0;
}

details summary {
  cursor: pointer;
  font-weight: 600;
  margin-top: 12px;
  margin-bottom: 8px;
}

details ul {
  list-style: none;
  padding-left: 0;
  margin-top: 8px;
}

details li {
  border-top: 1px solid #ddd;
  padding-top: 12px;
  margin-top: 12px;
}

details li img {
  max-width: 100px;
  margin-bottom: 8px;
  border-radius: 4px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 32px;
}

.pagination button {
  padding: 8px 16px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
}

.pagination button:disabled {
  background-color: #999;
  cursor: not-allowed;
}

.pagination span {
  font-size: 16px;
  font-weight: 500;
  color: #000;
}

@media (max-width: 768px) {
  .filters {
    flex-direction: column;
    align-items: center;
  }

  .filters input {
    width: 100%;
    max-width: 300px;
  }

  .order-card {
    padding: 12px;
  }

  .pagination {
    flex-direction: column;
    gap: 8px;
  }
}
