.auth-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 40px 24px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: white;
}

.back-button {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 16px;
  font-weight: 500;
  color: black;
  background: transparent;
  border: none;
  background-color: white;
  padding: 12px;
  border-radius: 50px;
}

.loginText {
  margin-bottom: 27px;
  font-size: 27px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.auth-form input {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.auth-form button {
  padding: 12px;
  border: none;
  background-color: black;
  color: white;
  font-weight: 600;
  cursor: pointer;
  border-radius: 4px;
}

.reset-password {
  text-align: right;
  font-size: 20px;
}

.reset-password:hover {
  color: #ccc;
}
