.reset-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f3f3f3;
}

.reset-box {
  background: white;
  padding: 32px;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.reset-box h2 {
  margin-top: 7px;
  margin-bottom: 16px;
  font-weight: 600;
  text-align: center;
}

.reset-box form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.reset-box input {
  padding: 11.2px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.reset-box button {
  padding: 12.8px;
  background-color: #007bff;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.25s ease;
}

.reset-box button:hover {
  background-color: #0056b3;
}

.success {
  color: green;
  text-align: center;
}

.error {
  color: red;
  text-align: center;
}
